<span class="token comment">// ...</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$options</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$reply</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">reply</span><span class="token punctuation">;</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="获取当前设置的回复规则" tabindex="-1"><a class="header-anchor" href="#获取当前设置的回复规则" aria-hidden="true">#</a> 获取当前设置的回复规则</h2>
